@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
	margin: 0;
	padding: 0;
	background: -webkit-linear-gradient(left, #3d3c3c, #181818);
	background: linear-gradient(to right, #3d3c3c, #181818);
	color: #a43931;
	font-family: 'Roboto', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

input::-webkit-inner-spin-button,
input::-webkit-calendar-picker-indicator {
	display: none;
	-webkit-appearance: none;
}

.App {
	margin: 0;
	padding: 0;
	width: 100%;
	height: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
}

.header {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
}

.header-logo {
	padding-top: 0px;
	margin-top: 10px;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.5);
	-webkit-transition: 0.3s;
	transition: 0.3s;
}

.header-logo-svg {
	margin-top: -40px;
}

.header-logo-svg-g {
	-webkit-transform: translateY(35%) translateX(33%);
	        transform: translateY(35%) translateX(33%);
}

.header-logo-name {
	margin-top: 0px;
}

.header-input {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
	padding: 10px;
	-webkit-flex-basis: 45%;
	        flex-basis: 45%;
}

.header-input-name {
	font-size: 22px;
	color: #a43931;
}

.header-input-box {
	font-family: inherit;
	border: 0;
	border-bottom: 2px solid #a43931;
	width: 25vw;
	font-size: inherit;
	text-align: center;
	background: transparent;
	color: white;
}

@media only screen and (max-device-width: 768px) {
	.header-input-box {
		width: 75vw;
	}
	.header-input {
		-webkit-flex-basis: 100%;
		        flex-basis: 100%;
	}
}

.header-input-box:focus {
	outline: 0;
}

.header-input-container {
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.5);
	margin-top: 10px;
	width: 60vw;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	-webkit-justify-content: space-around;
	        justify-content: space-around;
}

@media only screen and (max-device-width: 768px) {
	.header-input-container {
		width: 95vw;
	}
}

.header-input-submit {
	background: #a43931;
	color: white;
	border: none;
	border-radius: 3px;
	padding: 10px 10px 10px 10px;
	margin: 10px 5px 10px 5px;
	font: inherit;
	cursor: pointer;
	outline: none;
	min-width: 200px;
	width: 25%;
}

.emprestimos {
	margin-top: 20px;
	text-align: center;
	width: 60vw;
}

@media only screen and (max-device-width: 768px) {
	.emprestimos {
		width: 95vw;
	}
}

table {
	table-layout: fixed;
	width: 100%;
	border-collapse: collapse;
	overflow: hidden;
	box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
}

.no-prazo {
	background: -webkit-linear-gradient(right, #52c234, #061700);
	background: linear-gradient(to left, #52c234, #061700);
}

.hoje {
	background: -webkit-linear-gradient(right, #c0c234, #151700);
	background: linear-gradient(to left, #c0c234, #151700);
}

.vencido {
	background: -webkit-linear-gradient(right, #c23434, #170000);
	background: linear-gradient(to left, #c23434, #170000);
}

th,
td {
	padding: 15px 0 15px 0;
	background-color: rgba(255, 255, 255, 0.2);
	color: black;
	border-bottom: 1px solid black;
	word-wrap: break-word;
	overflow-wrap: break-word;
}

thead {
	background: -webkit-linear-gradient(right, #fff, #aaa);
	background: linear-gradient(to left, #fff, #aaa);
}

